.checkbox {
	position: relative;
	display: block;



	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		height: 19px;
		width: 19px;
		border: 1px solid #e3e3e3;
		border-radius: 1px; }

	input {
		position: absolute;
		opacity: 0;

		&:checked + label {

			&:before, &:after {
				opacity: 1; } } }

	label {
		position: relative;
		z-index: 2;
		font-size: 1rem;
		line-height: 1.25rem;
		padding-left: 32px;
		cursor: pointer;
		display: flex;
		background: none !important;

		&:before, &:after {
			content: "";
			position: absolute;
			background: #0f2645;
			transition: opacity .5s ease;
			opacity: 0; }

		&:before {
			height: 2px;
			width: 8px;
			top: 7px;
			left: 4px;
			transform-origin: left;
			transform: rotate(45deg); }

		&:after {
			height: 2px;
			width: 11px;
			top: 4px;
			left: 6px;
			transform-origin: right;
			transform: rotate(-45deg); } } }



